/**
 * 
 */
export default class SoundMgr {
    public static readonly soundResPath_csryw: string = "Sound/"
    public static readonly soundSpineResPath_csryw: string = "Spine/"

    //
    private static musicPath: string = "";

    //
    private static enabled_csryw: boolean = true;

    //
    public static isSoundEnabled_csryw() {
        return SoundMgr.enabled_csryw;
    }

    //
    public static setSoundEnabled_csryw(enabled: boolean) {
        SoundMgr.enabled_csryw = enabled;
        if (!SoundMgr.enabled_csryw) {
            SoundMgr.autoStopMusic_csryw();
        } else {
            SoundMgr.autoPlayMusic_csryw();
        }
    }

    /**
     * 
     * resources/Sound/aaa.mp3  playBGM("aaa")
     * @param name  .mp3
     */
    public static playMusic_csryw(name: string) {
        SoundMgr.musicPath = name;
        if (!SoundMgr.enabled_csryw)
            return;
        let url = SoundMgr.getSoundUrl_csryw(name);
        //
        cc.resources.load(url, cc.AudioClip, function (err, clip: cc.AudioClip) {
            if (err) {
                LogUtils.error_csryw(err);
                return;
            }
            cc.audioEngine.playMusic(clip, true);
        });
    }


    /**
     *  
     */
    public static autoPlayMusic_csryw() {
        if (SoundMgr.musicPath != "") {
            SoundMgr.playMusic_csryw(SoundMgr.musicPath);
        } else {
            // console.log("  ")
        }
    }

    /**
     *   
     */
    public static autoStopMusic_csryw() {
        cc.audioEngine.stopMusic();
    }

    /**
     * 
     */
    public static stopMusic_csryw() {
        SoundMgr.musicPath = "";
        cc.audioEngine.stopMusic();
    }

    /**
     * 
     * resources/Sound/aaa.mp3  playSound("aaa")
     * @param name  .mp3
     */
    public static playSound_csryw(name: string) {
        if (!SoundMgr.enabled_csryw) {
            return;
        }
        let url = SoundMgr.getSoundUrl_csryw(name);
        //console.log(url)
        cc.resources.load(url, cc.AudioClip, function (err, clip: cc.AudioClip) {
            if (err) {
                LogUtils.error_csryw(err);
                return;
            }
            let audioID_csryw = cc.audioEngine.play(clip, false, 1);
        });
    }

    /**
     * spine 
     * @param eventStringValue  +    resources/Spine/123/aaa.mp3  playSoundResources_csryw("123/aaa")
     * @param animationName 
     */
    public static playSpineSound_csryw(eventStringValue: string, animationName: string) {
        if (!SoundMgr.enabled_csryw) {
            return;
        }
        let url = SoundMgr.soundSpineResPath_csryw + animationName;
        //console.log(url)
        cc.resources.load(url, cc.AudioClip, function (err, clip: cc.AudioClip) {
            if (err) {
                LogUtils.error_csryw(err);
                return;
            }
            let audioID_csryw = cc.audioEngine.play(clip, false, 1);
        });
    }



    public static getSoundUrl_csryw(name: string): string {
        let url = SoundMgr.soundResPath_csryw + name;
        return url;
    }
}
