/**
 * 
 */
export class LogUtils {
    /** log */
    private static _logStatus_csryw: boolean = true;

    /**  */
    public static setLogStatus(flag: boolean): void {
        LogUtils._logStatus_csryw = flag;
    }

    /***/
    public static log_csryw(message?: any, ...optionalParams: any[]): void {
        if (!LogUtils._logStatus_csryw) { return; }
        console.log("[log][", LogUtils.getFullTime_csryw(), "]", ":", message, ...optionalParams);
    }

    /***/
    public static info_csryw(message?: any, ...optionalParams: any[]): void {
        if (!LogUtils._logStatus_csryw) { return; }
        console.debug("[debug][", LogUtils.getFullTime_csryw(), "]", ":", message, ...optionalParams);
    }


    /***/
    public static warn_csryw(message?: any, ...optionalParams: any[]): void {
        if (!LogUtils._logStatus_csryw) { return; }
        console.warn("[warn][", LogUtils.getFullTime_csryw(), "]", ":", message, ...optionalParams);
    }


    /***/
    public static error_csryw(message?: any, ...optionalParams: any[]): void {
        if (!LogUtils._logStatus_csryw) { return; }
        console.error("[error][", LogUtils.getFullTime_csryw(), "]", ":", message, ...optionalParams);
    }

    /***/
    public static networkLog_csryw(message?: any, ...optionalParams: any[]): void {
        if (!LogUtils._logStatus_csryw) { return; }
        console.log("[network][", LogUtils.getFullTime_csryw(), "]", ":", message, ...optionalParams);
    }
    /***/
    public static networkError_csryw(message?: any, ...optionalParams: any[]): void {
        if (!LogUtils._logStatus_csryw) { return; }
        console.error("[network][", LogUtils.getFullTime_csryw(), "]", ":", message, ...optionalParams);
    }

    /**  */
    public static getFullTime_csryw(): string {
        let date = new Date();
        return date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds() + "-" + date.getMilliseconds();
    }
}

//
(<any>window).LogUtils = LogUtils;
