
import AppPlatform from "../Util/AppPlatform";
import { LogUtils } from "../Util/LogUtils";
import EventMgr from "../Event/EventMgr";
import { ryw_Event } from "../Event/EventEnum";


/**
 * ,
 */
enum StatsFrameWorkEvent {

    ReportAdClickSuccess_csryw = "ReportAdClickSuccess",//
    ReportAdClickFail_csryw = "ReportAdClickFail",//
    ReportAdClickStart_csryw = "ReportAdClickStart",//
    ReportLaunchOptions_csryw = "ReportLaunchOptions",//ip
    LogReportInfo_csryw = "LogReportInfo",//
    LogReportError_csryw = "LogReportError",//
    LoginReportInfo_csryw = "LoginReportInfo",//
    GameEventLVInto_csryw = "Event_LVInto", //    level
    GameEventLVFinish_csryw = 'Event_LVFinish', //    level status            win,lose   duration

    Event_Commerce_csryw = "Event_Commerce",//
    Event_Custom_csryw = "Event_Custom",//


}


/*
 * @Author: YeeChan
 * @Date: 2020-11-24 19:30:12
 * @Description: 
 */
export class UmengMgr {



    /**
     *  
     *  StatsMgr.sendEvent("ID")
     *  
     *      StatsMgr.sendEvent('ID', { '1':'1','2':'2' }); // 
     *      StatsMgr.sendEvent('ID', { 'PayAmount':6999 });// 
     * @param event  ID
     * @param params  
     */
    public static sendEvent_csryw(event: string, params?: { [key: string]: string | number }) {
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            UmengMgr.uma_trackEvent(event, params);
        } else if (AppPlatform.is_Iphone_csryw() || AppPlatform.is_Android_csryw()) {//ios
            // FMApp.uma_trackEvent_csryw(event, params);
        }
        EventMgr.emitEvent_csryw(ryw_Event.ryw_Umeng_csryw, { event: event });
    }

    /**
     *  IP 
     * @param scene 
     * @param dqip 
     * @param ipxq 
     */
    public static sendReportLaunchOptions_csryw(scene: string, dqip: string, ipxq: string) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.ReportLaunchOptions_csryw, {
            "scene": scene,
            "dqip": dqip,
            "ipxq": ipxq,
        })
    }


    /**
     *  
     * @param title 
     * @param appid 
     */
    public static sendReportAdClickSuccess_csryw(title: string, appid: string | number) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.ReportAdClickSuccess_csryw, {
            "title": title,
            "appid": String(appid)
        })
    }


    /**
     *  
     * @param title 
     * @param appid 
     */
    public static sendReportAdClickFail_csryw(title: string, appid: string | number) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.ReportAdClickFail_csryw, {
            "title": title,
            "appid": String(appid)
        })
    }

    /**
     * 
     * @param title 
     * @param appid 
     */
    public static sendReportAdClickStart_csryw(title: string, appid: string | number) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.ReportAdClickStart_csryw, {
            "title": title,
            "appid": String(appid)
        })
    }


    /**
     * 
     * @param level id
     */
    public static sendGameEventLvInto_csryw(level: string) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.GameEventLVInto_csryw, {
            "level": level
        })

        //
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            window["wx"].uma.stage.onStart({
                stageId: level + '',//
                stageName: '' + level + '',//   number
            })
        }
    }

    /**
     *    ,win
     * @param level id
     * @param isWin  true/false
     * @param duration ()
     * @param custom 
     *          isHome:0 1
     *          
     */
    public static sendGameEventLvFinish_csryw(level: string, isWin: boolean, duration: number, custom?: { [key: string]: string | number }) {
        let status = "win";
        if (!isWin) {
            status = "lose";
        }
        let params = {};
        params["level"] = level;
        params["status"] = status;
        params["duration"] = duration;
        if (custom) {
            for (const key in custom) {
                if (Object.prototype.hasOwnProperty.call(custom, key)) {
                    const element = custom[key];
                    if (key == "status") {
                        if (element) {
                            params["status"] = "home";
                        }
                    } else {
                        params[key] = element;
                    }
                }
            }
        }
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.GameEventLVFinish_csryw, params)

        //
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            let _um_sdu = 0
            if (duration) {
                _um_sdu = duration * 1000;//
            }
            window["wx"].uma.stage.onEnd({
                stageId: level + '',//
                stageName: '' + level + '',//   number
                event: isWin ? "complete" : "fail",///
                _um_sdu: _um_sdu //
            })
        }
    }


    /**
     * 
     */
    public static sendEvent_Commerce_csryw(params: { [key: string]: string | number }) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.Event_Commerce_csryw, params)
    }
    /**
     * 
     */
    public static sendEvent_Custom_csryw(params: { [key: string]: string | number }) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.Event_Custom_csryw, params)
    }


    /**
     * 
     * @param info 
     */
    public static sendLogReportInfo_csryw(info: string) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.LogReportInfo_csryw, {
            "info": info
        })
    }

    /**
     * 
     * @param info 
     */
    public static sendLogReportError_csryw(info: string) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.LogReportError_csryw, {
            "info": info
        })
    }

    /**
     * 
     * @param type //
     * @param state 012
     * @param info 
     */
    public static sendLoginReportInfo_csryw(type: string, state: number, info: string) {
        UmengMgr.sendEvent_csryw(StatsFrameWorkEvent.LoginReportInfo_csryw, {
            "type": type,
            "state": state,
            "info": info,
        })
    }


    /**
     * 
     * @param event 
     * @param params 
     */
    private static uma_trackEvent(event: string, params?: { [key: string]: string | number }) {
        if (AppPlatform.is_WECHAT_GAME_csryw()) {// https://umini.shujupie.com
            if (window["wx"].uma) {
                window["wx"].uma.trackEvent(event, params);
            } else {
                LogUtils.error_csryw(" ");
            }
        }
    }

}
