    //
    public static loginPlatform_csryw(successListener: Function, failListener: Function, pcListener?: Function) {
        if (AppPlatform.is_KS_csryw()) {
            KSAPI.login_csryw(
                code => {
                    successListener(code);
                },
                fail => {
                    failListener(fail);
                }
            );
        }
        else if (AppPlatform.is_TT_GAME_csryw()) {//
            TTAPI.ttLogin_csryw(function (code) {
                successListener(code)
            }, () => {
                failListener();
            })
        } else if (AppPlatform.is_WECHAT_GAME_csryw()) {
            WXAPI.wxLogin_csryw(function (code) {
                successListener(code)
            }, null)

        }
        else if (AppPlatform.is_OPPO_GAME_csryw()) { //oppo
            OPPOAPI.initAdService_csryw(() => { }, () => { }, () => { });
            OPPOAPI.Login_csryw(function (code) {
                successListener(code)
            }, null)
        }
        else if (AppPlatform.is_QQ_PLAY_csryw()) {  //qq
            QQMiniGameAPI.Login_csryw(function (code) {
                successListener(code)
            }, null)
        }
        else if (AppPlatform.is_VIVO_GAME_csryw()) {//VIVO 
            let failCounter = 0;
            let _successListener = (code: any) => {
                successListener(code)
            }
            let fail = () => {
                if (failCounter >= 1) {
                    console.log("vivo ");
                    failListener();
                    return;
                }
                VIVOAPI.showDialog_csryw("", "", [{ text: '', color: '#33dd44' }], () => {
                    VIVOAPI.Login_csryw(function (token, type) { _successListener(token); }, function () { fail(); });
                    ++failCounter;
                }, () => { }, () => { });
            }
            VIVOAPI.Login_csryw(function (token, type) { _successListener(token); }, function () { fail(); });
        }

        // else if (AppPlatform.is_BD_GAME_csryw()) {//
        //     BDAPI.bdLogin_csryw((code) => {
        //         successListener(code)
        //     }, null);
        // }
        else if (AppPlatform.is_Android_csryw() || AppPlatform.is_Iphone_csryw()) {//android  ios
            LogUtils.log_csryw("playVideo android ios ")
        } else {
            pcListener ? pcListener() : failListener();
        }
    }

