import { FMListener, callFM_csryw } from "../Interface/FMInterface";

export default abstract class FMViewBase extends cc.Component {

    //
    protected _isInit_csryw: number = 0;

    protected _listenerCallView_csryw: FMListener;

    //
    public abstract EventEnumView_csryw = {};


    //------------------------------------------

    protected onLoad() {
        this._initView_csryw();
    }

    //view onLoad 
    public _initView_csryw(): boolean {
        if (this._isInit_csryw != 0) {
            return;
        }
        this._isInit_csryw = 1;
        this.initView_csryw();
        this.addEvent_csryw();
        this._isInit_csryw = 2;
        return true;
    }


    //view   onLoad
    protected abstract initView_csryw(): void;

    // initView
    protected abstract addEvent_csryw(): void;

    //  removeView  
    protected abstract removeEvent_csryw(): void;

    //
    public onListenerEventView_csryw(listener: FMListener) {
        this._listenerCallView_csryw = listener;
    }

    //
    protected emitListenerEvent_csryw(event: string) {
        console.log(" " + event)
        if (this._listenerCallView_csryw) {
            callFM_csryw(this._listenerCallView_csryw, event, this);
        }
    }

    /**
     * 
     */
    public removeView_csryw(): void {
        this.onDestroy();
        this.node.destroy();
    }

    /**
     * view
     */
    public hideView_csryw(): void {
        this.node.active = false;
    }

    /**
     * view
     */
    public showView_csryw(): void {
        this.node.active = true;
    }

    /**
     * view 
     */
    public isActiveView_csryw(): boolean {
        if (cc.isValid(this, true) && this.node.activeInHierarchy) { // && 
            return true;
        }
        return false;
    }

    /**
     * banner
     */
    public showBanner_csryw() {

    }

    /**
     * banner
     */
    public hideBanner_csryw() {

    }

    //
    protected onEnable(): void {

    }

    //
    protected onDisable(): void {
        this.hideBanner_csryw();
    }

    //
    protected onDestroy(): void {
        this.removeEvent_csryw();
        this.hideBanner_csryw();
    }

}