const { ccclass, property } = cc._decorator;

@ccclass
export default class Adapt extends cc.Component {
    @property()
    isByChange = true;
    onLoad() {
        if (this.isByChange) {
            cc.view.setResizeCallback(() => this.resize())
        }
        this.resize();
    }

    public curDR: cc.Size;
    public resize() {

        var cvs = cc.find('Canvas').getComponent(cc.Canvas);
        //
        if (!this.curDR) {
            this.curDR = cvs.designResolution;
        }
        var dr = this.curDR;
        var s = cc.view.getFrameSize();
        var rw = s.width;
        var rh = s.height;
        var finalW = rw;
        var finalH = rh;

        if ((rw / rh) > (dr.width / dr.height)) {
            //!#zh:  */
            //cvs.fitHeight = true;

            //
            finalH = dr.height;
            finalW = finalH * rw / rh;
        }
        else {
            /*!#zh:  */
            //cvs.fitWidth = true;
            //
            finalW = dr.width;
            finalH = rh / rw * finalW;
        }
        cvs.designResolution = cc.size(finalW, finalH);
        cvs.node.width = finalW;
        cvs.node.height = finalH;
        cvs.node.emit('resize');
    }
}