import TTAPI from "../../../Platform/tt/TTAPI";
import FMComponentExtend from "../../Base/FMComponentExtend";
import { ryw_Event } from "../../Event/EventEnum";
import EventMgr from "../../Event/EventMgr";
import DialogMgr from "../../Mgr/DialogMgr";
import PhysicalPowerMgr from "../../Mgr/PhysicalPowerMgr";

/*
 * @Author: YeeChan
 * @Date: 2022-01-11 16:27:28
 * @Description:
 */
const { ccclass, property } = cc._decorator;

@ccclass
export default class PowerNode extends FMComponentExtend {
    protected EventEnumView_csryw: { [key: string]: string; } = {}

    //
    private labelPower: cc.Label = null;
    //
    private labelPowerTime: cc.Label = null;
    //
    private labelPowerAll: cc.Label = null;

    protected initView_csryw(): void {
        let bgNode = this.node.getChildByName("bgNode");
        this.onClick(bgNode, () => {
            if (PhysicalPowerMgr.getPhyVideoAllTime() <= 0
                && PhysicalPowerMgr.getPhysicalNum() < PhysicalPowerMgr.MaxPhysical) {//
                // 
                PhysicalPowerMgr.openAddPowerDialog(handleFM_csryw(() => { }, this), true);
            } else {
                //
                PhysicalPowerMgr.openAddPowerDialog(handleFM_csryw(() => { }, this), false);
            }
        }, this)

        this.labelPower = this.child(bgNode, "labelPower", cc.Label);
        this.labelPowerTime = this.child(bgNode, "labelPowerTime", cc.Label);
        this.labelPowerAll = this.child(bgNode, "labelPowerAll", cc.Label);
        this.labelPower.node.active = true;
        this.labelPowerTime.node.active = true;
        this.labelPowerAll.node.active = true;

        //
        EventMgr.onEvent_csryw(ryw_Event.updatePhysicalPower, (tab) => {
            this.updatePhysicalPower();
        }, this);
        this.updatePhysicalPower();
    }

    /**
     * 
     */
    private updatePhysicalPower() {
        if (PhysicalPowerMgr.getPhyVideoAllTime() > 0) {//
            this.labelPowerTime.string = ""
            this.labelPower.string = "";

            let time = PhysicalPowerMgr.getPhyVideoAllTime();
            let ntime = new Date().getTime();
            let cctime = time - ntime;
            this.labelPowerAll.string = PhysicalPowerMgr.formatTime(cctime / 1000);
        } else {//
            this.labelPowerAll.string = "";
            this.labelPower.string = PhysicalPowerMgr.getPhysicalNum() + "/" + PhysicalPowerMgr.MaxPhysical;
            if (PhysicalPowerMgr.getPhysicalNum() < PhysicalPowerMgr.MaxPhysical) {

                let time = PhysicalPowerMgr.getPhyVideoTime();
                let ntime = new Date().getTime();
                let maxTime = PhysicalPowerMgr.RecoverPhysicalTime * 1000
                let cctime = maxTime - (ntime - time);
                if (cctime > 0) {
                    this.labelPowerTime.string = PhysicalPowerMgr.formatTime3(cctime / 1000);
                } else {
                    this.labelPowerTime.string = ""
                }
            } else {
                this.labelPowerTime.string = ""
            }
        }
    }

}