    //-----------------------------------------------------------
    public static showInterstitialAd_csryw(onAdClose: Function, onFailed: Function) {
        if (AppPlatform.is_QQ_PLAY_csryw()) {
            var interstitialAd = window["qq"].createInterstitialAd({
                adUnitId: AppConfig.qq_InsAdUnitId_csryw,
            })

            let _onLoad = () => {
                console.log(' ');
                interstitialAd.show().catch((err) => {
                    console.log('  ' + err)
                    interstitialAd.offLoad(_onLoad);
                    interstitialAd.offError(_onError);
                    interstitialAd.offClose(_onClose);
                    interstitialAd.destroy();
                    if (onFailed) {
                        onFailed();
                    }
                })
            }
            interstitialAd.onLoad(_onLoad)

            let _onError = (err) => {
                console.log(' ' + err);
                interstitialAd.offLoad(_onLoad);
                interstitialAd.offError(_onError);
                interstitialAd.offClose(_onClose);
                interstitialAd.destroy();
                if (onFailed) {
                    onFailed();
                }
            }
            interstitialAd.onError(_onError)

            let _onClose = () => {
                console.log(' ');
                interstitialAd.offLoad(_onLoad);
                interstitialAd.offError(_onError);
                interstitialAd.offClose(_onClose);
                interstitialAd.destroy();
                if (onAdClose) {
                    onAdClose();
                }
            }
            interstitialAd.onClose(_onClose)
        }
        else {
            onAdClose();
        }
    }

    //-----------------------------------------------------------
    public static mAppboxAd_csryw = null;
    protected static onBoxAdClose_csryw: Function = null;

    public static LoadAppBoxAd_csryw(onAdClose: Function, onFailed: Function) {
        if (AppPlatform.is_QQ_PLAY_csryw()) {
            this.mAppboxAd_csryw = window["qq"].createAppBox({
                adUnitId: AppConfig.qq_AppBoxId_csryw,
            })
            this.mAppboxAd_csryw.load().then(() => {
                console.log(' ');
            })

            this.mAppboxAd_csryw.onError((err) => {
                console.log(' ' + err);
                if (onFailed) {
                    onFailed();
                    onFailed = null;
                }
            })
            this.onBoxAdClose_csryw = () => {
                console.log(' ');
                if (onAdClose) {
                    onAdClose();
                    onAdClose = null;
                }
            }
            this.mAppboxAd_csryw.onClose(this.onBoxAdClose_csryw);
        }
        else {
            if (onAdClose) {
                onAdClose();
                onAdClose = null;
            }
        }
    }

    public static showAppBoxAd_csryw(onFailed: Function, onAdClose?: Function) {
        if (AppPlatform.is_QQ_PLAY_csryw()) {
            if (QQMiniGameAPI.mAppboxAd_csryw) {
                console.log("");
                QQMiniGameAPI.mAppboxAd_csryw.offClose(QQMiniGameAPI.onBoxAdClose_csryw);
                QQMiniGameAPI.onBoxAdClose_csryw = () => {
                    console.log(' ');
                    if (onAdClose) {
                        onAdClose();
                        onAdClose = null;
                    }
                }
                QQMiniGameAPI.mAppboxAd_csryw.onClose(QQMiniGameAPI.onBoxAdClose_csryw);
                QQMiniGameAPI.mAppboxAd_csryw.show().catch((err) => {
                    console.log('  ' + err);
                    if (onFailed) {
                        onFailed();
                        onFailed = null;
                    }
                })
            } else {
                QQMiniGameAPI.LoadAppBoxAd_csryw(onAdClose, onFailed);
            }
        }
    }

