    //QQ1IP
    //data   { Complete : Function , PrizeCount : String | Number}
    //onSuccess 
    //onFail 
    public static tryShowWXCrazyClick_csryw(): boolean {
        let launchScene = QQMiniGameAPI.getLaunchOptionsSync_csryw().scene;
        let noEnterBySearch: boolean = true;
        let wudianSceneList = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianSceneList_csryw;
        for (let i = 0; i < wudianSceneList.length; ++i) {
            let wudianSceneValue = wudianSceneList[i];
            if (launchScene == wudianSceneValue) {
                noEnterBySearch = false;
            }
        }
        let ipBlocked = wudianMgr.ryw_GetIpBlocked_csryw();
        let wudian = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudian_csryw;
        let kuangdianBanner = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().qqcfg_csryw.kuangdianBanner_csryw;
        if (AppConfig.Versions_csryw == AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().qqcfg_csryw.qqversions_csryw
            && ipBlocked && noEnterBySearch && 1 == wudian && 1 == kuangdianBanner) {
            return true;
        }
        else {
            return false;
        }
    }

