    private static mAppBlockAd_csryw = null;
    private static onFail: Function;

    private static screenWidth_csryw: number = NaN;
    private static screenHeight_csryw: number;
    private static pixelRatio_csryw: number;
    private static isIos_csryw: boolean;
    private static isAppBlockAdLoading_csryw: boolean = false;
    private static skdVersion_csryw: string;
    private static supportSDKVersion_csryw = "1.15.0";
    /**
     * 
     * @param onFail 
     * @param centerX centerX px
     * @param top top px
     * @param orientation landscape | vertical
     */
    public static showAppBlockAd_csryw(onFail?: Function, top: number = 150, orientation: string = "landscape") {
        if (!AppPlatform.is_QQ_PLAY_csryw()) return;
        if (!window["qq"].createBlockAd) return;
        if (QQMiniGameAPI.isAppBlockAdLoading_csryw) return;

        QQMiniGameAPI.isAppBlockAdLoading_csryw = true;
        if (isNaN(QQMiniGameAPI.screenWidth_csryw)) {
            try {
                let res = window["qq"].getSystemInfoSync();

                QQMiniGameAPI.screenWidth_csryw = res.windowWidth;
                QQMiniGameAPI.screenHeight_csryw = res.windowHeight;
                QQMiniGameAPI.pixelRatio_csryw = res.pixelRatio;
                QQMiniGameAPI.isIos_csryw = res.platform == "ios";
                QQMiniGameAPI.skdVersion_csryw = res.SDKVersion;

                //IOSDP,left,topDPandroidPX
                QQMiniGameAPI.screenWidth_csryw *= (QQMiniGameAPI.isIos_csryw ? 1 : QQMiniGameAPI.pixelRatio_csryw);
                QQMiniGameAPI.screenHeight_csryw *= (QQMiniGameAPI.isIos_csryw ? 1 : QQMiniGameAPI.pixelRatio_csryw);

                console.log("getSystemInfoSync ==> ", res.SDKVersion);
            } catch (e) {
                if (onFail) onFail();
                return;
            }
        }

        let arr1: Array<number> = QQMiniGameAPI.skdVersion_csryw.split(".").map(v => parseInt(v));
        let arr2: Array<number> = QQMiniGameAPI.supportSDKVersion_csryw.split(".").map(v => parseInt(v));
        let isSupport: boolean = true;
        for (let i: number = 0; i < arr1.length; i++) {
            if (arr1[i] < arr2[i]) {
                isSupport = false;
                break;
            }
        }
        if (!isSupport) return;

        console.log("QQMiniGameAPI.showAppBlockAd ", top);

        QQMiniGameAPI.destroyAppBlockAd_csryw();
        QQMiniGameAPI.onFail = onFail;


        let min: number = QQMiniGameAPI.isIos_csryw ? 32 / QQMiniGameAPI.pixelRatio_csryw : 32;
        let mTop: number = Math.max(min, top / (QQMiniGameAPI.isIos_csryw ? QQMiniGameAPI.pixelRatio_csryw : 1));
        let mLeft: number = QQMiniGameAPI.screenWidth_csryw / 2;
        mLeft = min;

        QQMiniGameAPI.mAppBlockAd_csryw = window["qq"].createBlockAd({
            adUnitId: AppConfig.qq_blockAdArray_csryw[Math.floor(Math.random() * AppConfig.qq_blockAdArray_csryw.length)],
            style: { left: mLeft, top: mTop },
            size: QQMiniGameAPI.AppBlockSize_csryw,
            orientation: orientation,
        });
        // QQMiniGameAPI.mAppBlockAd.onResize(QQMiniGameAPI.appBlockADResize);
        QQMiniGameAPI.mAppBlockAd_csryw.onError(QQMiniGameAPI.appBlockADError_csryw);
        QQMiniGameAPI.mAppBlockAd_csryw.show().catch((err) => {
            console.log('  ' + JSON.stringify(err));
            if (onFail) {
                onFail();
            }
        });
        QQMiniGameAPI.isAppBlockAdLoading_csryw = false;
    }

    private static appBlockADResize_csryw(obj: any): void {
        if (!QQMiniGameAPI.mAppBlockAd_csryw["style"]) return;

        let realWidth = obj.width;
        let realHeight = obj.height;

        // console.log("onResize ==> qq ", screenWidth, "=", realWidth, "=", screenHeight, "=", realHeight);

        // if (!isNaN(centerX)) {
        //     mLeft = ((screenWidth - realWidth) / 2) + (centerX / (isIos ? pixelRatio : 1));
        // } else if (!isNaN(left)) {
        //     left /= (isIos ? pixelRatio : 1);
        //     mLeft = Math.max(33, left);
        // } else if (!isNaN(right)) {
        //     right /= (isIos ? pixelRatio : 1);
        //     mLeft = Math.max(screenWidth - realWidth, right);
        // }

        // console.log("onResize ==> 3 ", mLeft, "=", mTop);
        // QQMiniGameAPI.mAppBlockAd.style.left = mLeft;
        let mLeft = (QQMiniGameAPI.screenWidth_csryw - realWidth) / 2;
        QQMiniGameAPI.mAppBlockAd_csryw.style.left = mLeft;
    }

    private static appBlockADError_csryw(err: any): void {
        console.log("   ", JSON.stringify(err));
        if (QQMiniGameAPI.onFail) QQMiniGameAPI.onFail();
    }

    public static destroyAppBlockAd_csryw() {
        if (!AppPlatform.is_QQ_PLAY_csryw()) return;
        if (!QQMiniGameAPI.mAppBlockAd_csryw) return;
        console.log("QQMiniGameAPI.destroyAppBlockAd");
        QQMiniGameAPI.mAppBlockAd_csryw.offResize(QQMiniGameAPI.appBlockADResize_csryw);
        QQMiniGameAPI.mAppBlockAd_csryw.offError(QQMiniGameAPI.appBlockADError_csryw);
        QQMiniGameAPI.mAppBlockAd_csryw.hide();
        QQMiniGameAPI.mAppBlockAd_csryw.destroy();
        QQMiniGameAPI.mAppBlockAd_csryw = null;


    }
