export default class WXAPI {


    public static readonly GameRecorder_csryw = new GameRecorder();

    public static wxLogin_csryw(onSuccess: Function, onFail: Function) {
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            window["wx"].login(
                {
                    success: (res) => {
                        if (res.code) {
                            let code = res.code;
                            console.log(",code : " + code)
                            onSuccess(code);
                        }
                    }
                })
        }
    }


    //----------------------------------------------------------
    protected static _isRegRewardedVideoAdEvent_csryw = false;
    protected static _onRewardedVideoAdFailed_csryw: Function = null;
    protected static _onRewardedVideoAdClose_csryw: Function = null;
    protected static onRewardedVideoAdLoad_csryw() {
        console.log(' ')
    }
    protected static onRewardedVideoAdError_csryw(err) {
        console.log(' ' + err)
        console.log(JSON.stringify(err));
        
        if (WXAPI._onRewardedVideoAdFailed_csryw) {
            WXAPI._onRewardedVideoAdFailed_csryw();
        }
    }
    protected static onRewardedVideoAdClose_csryw(res) {
        if ((res && res.isEnded) || res == null) {
            console.log(' ')
            if (WXAPI._onRewardedVideoAdClose_csryw) {
                WXAPI._onRewardedVideoAdClose_csryw(true)
            }
        }
        else {
            console.log(' ')
            if (WXAPI._onRewardedVideoAdClose_csryw) {
                WXAPI._onRewardedVideoAdClose_csryw(false)
            }
        }
    }
    protected static regRewardedVideoAdEvent_csryw(rewardedVideoAd) {

        rewardedVideoAd.onLoad(WXAPI.onRewardedVideoAdLoad_csryw)
        rewardedVideoAd.onError(WXAPI.onRewardedVideoAdError_csryw)
        rewardedVideoAd.onClose(WXAPI.onRewardedVideoAdClose_csryw)

        WXAPI._isRegRewardedVideoAdEvent_csryw = true;
    }

    public static showRewardedVideoAd_csryw(onAdClose: Function, onFailed: Function, id?: string) {
        let num = Math.floor(Math.random() * AppConfig.adUnitId_csryw.length)
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            WXAPI._onRewardedVideoAdClose_csryw = onAdClose;
            WXAPI._onRewardedVideoAdFailed_csryw = onFailed;
            let videoId = AppConfig.adUnitId_csryw[num]
            if (id) {
                videoId = id;
            }
            let rewardedVideoAd = window["wx"].createRewardedVideoAd(
                {
                    adUnitId: videoId,
                }
            );

            if (!WXAPI._isRegRewardedVideoAdEvent_csryw) {
                WXAPI.regRewardedVideoAdEvent_csryw(rewardedVideoAd);
            }

            rewardedVideoAd.load().then(() => {
                let promise = rewardedVideoAd.show();
                promise.then(() => console.log(' '));
                promise.catch((err) => {
                    rewardedVideoAd.load()
                        .then(() => rewardedVideoAd.show())
                        .catch(err => {
                            console.log(' ')
                        })
                });
            }).catch(err => {
                console.log(' ')
            })
        }
        else {
            onAdClose(true);
        }
    }
    //----------------------------------------------------------------


    //----------------------------------------------------
    public static navigateToMiniProgram_csryw(appId: string, path: string, onSuccess: Function, onFail: Function, onComplate: Function) {
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            console.log(" " + appId);
            window["wx"].navigateToMiniProgram(
                {
                    appId: appId,
                    path: path,
                    extraData: {
                        foo: 'bar'
                    },
                    envVersion: 'release',
                    success(res) {
                        if (onSuccess) {
                            onSuccess(res)
                        }
                    },
                    fail(res) {
                        if (onFail) {
                            onFail(res)
                        }
                    },
                    complete(res) {
                        if (onComplate) {
                            onComplate(res)
                        }
                    }
                })

        }
    }
    //----------------------------------------------------------------------

    //-------------------------------------------------------------
    protected static _onShow_csryw: Function = null;
    protected static _lastShareTime_csryw: number = 0;
    public static share_csryw(complate: Function, titel: string, imageUrl: string) {

        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            WXAPI._onShow_csryw = () => {
                window["wx"].offShow(WXAPI._onShow_csryw)
                WXAPI._onShow_csryw = null;
                var c = Date.now() - this._lastShareTime_csryw;
                if (complate) {
                    if (Date.now() - this._lastShareTime_csryw > 2000) {
                        complate(true)
                    }
                    else {
                        complate(false)
                    }
                }
            }
            window["wx"].onShow(WXAPI._onShow_csryw)
            this._lastShareTime_csryw = Date.now();
            window["wx"].shareAppMessage(
                {
                    title: titel,
                    imageUrl: imageUrl
                }
            );
        }
    }
    //----------------------------------------------------------------------


    //-----------------------------------------------------------
    public static showInterstitialAd_csryw(onAdClose: Function, onFailed: Function) {
        let num = Math.floor(Math.random() * AppConfig.InsAdUnitId_csryw.length)
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            var interstitialAd = window["wx"].createInterstitialAd({
                adUnitId: AppConfig.InsAdUnitId_csryw[num],
            })

            interstitialAd.onLoad(() => {
                console.log(' ');
                interstitialAd.show().catch((err) => {
                    console.log('  ' + err)
                    if (onFailed) {
                        onFailed();
                    }
                })
            })

            interstitialAd.onError((err) => {
                console.log(' ' + err);
                if (onFailed) {
                    onFailed();
                }
            })

            interstitialAd.onClose(() => {
                console.log(' ');
                if (onAdClose) {
                    onAdClose();
                }
            })
        }
        else {
            onAdClose();
        }
    }
    /**
     * Object
     * scene	number	
     * query	Object	 query 
     * shareTicket	string	shareTicket
     * referrerInfo	object	 App  {}
     * https://developers.weixin.qq.com/minigame/dev/api/base/app/life-cycle/wx.getLaunchOptionsSync.html
     * @static
     * @returns {LaunchOptions} 
     * @memberof WXAPI
     */
    public static getLaunchOptionsSync_csryw() {
        // let result = { scene: 0, query: null, shareTicket: "", referrerInfo: null };

        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            let obj = window["wx"].getLaunchOptionsSync()
            console.log(" " + obj.scene);
            let str = JSON.stringify(obj.query);
            console.log("Query " + str);
            let key = obj.query["key"];
            console.log("Querykey " + key);
            console.log("ShareTicket " + obj.shareTicket);
            console.log("ReferrerInfo.appId " + obj.referrerInfo.appId);
            console.log("ReferrerInfo.extraData " + obj.referrerInfo.extraData);
            return obj;
        }
        let obj = { scene: 1001, query: "", shareTicket: "", appId: "", extraData: "" }
        return obj;
    }

    //----------------------------------------------------------------------
    /**
     * ,
     * 
     * 
     * @static
     * @param {string} titel 
     * @param {string} imageUrl 
     * @param {Function} [success] ()
     * @param {Function} [fail] ()
     * @param {Function} [complate] ()
     * @memberof WXAPI
     */
    public static SetShareMenu_csryw(titel: string, imageUrl: string, success?: Function, fail?: Function, complate?: Function) {

        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            console.log("");
            window["wx"].showShareMenu({
                withShareTicket: false,
                success: success,
                fail: fail,
                complete: complate
            });
            window["wx"].onShareAppMessage(function () {
                return {
                    title: titel,
                    imageUrl: imageUrl
                }
            });
        }
    }

    //
    public static checkUpdate_csryw() {

        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            var updateManager = window["wx"].getUpdateManager()
            updateManager.onCheckForUpdate(function (res) {
                console.log(" : ", res.hasUpdate)
            })
            updateManager.onUpdateReady(function () {
                window["wx"].showModal({
                    title: '',
                    content: '',
                    success: function (res) {
                        if (res.confirm) {
                            updateManager.applyUpdate()
                        }
                    }
                })
            })
            updateManager.onUpdateFailed(function () {
                console.log("!!!")
            })
        }
    }

    protected static _crazyClickShowCounter_csryw: number = 0;

    //
    public static tryShowWXCrazyClick_csryw(): boolean {
        if (!wudianMgr.wudianFlag_csryw || 1 != AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wxcfg_csryw.kuangdianBanner_csryw) {
            return false;
        }
        let kuangdianLevelSpcacing = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wxcfg_csryw.kuangdianLevelSpcacing_csryw;
        if (kuangdianLevelSpcacing == 0) {
            return true;
        }
        console.log(' WXAPI._crazyClickShowCounter_csryw = ' + WXAPI._crazyClickShowCounter_csryw + ' (kuangdianLevelSpcacing - 1) = ' + (kuangdianLevelSpcacing - 1));
        if (WXAPI._crazyClickShowCounter_csryw == (kuangdianLevelSpcacing - 1)) {
            WXAPI._crazyClickShowCounter_csryw = 0;
            return true;
        }
        WXAPI._crazyClickShowCounter_csryw++;
        return false;
    }

}

