import FMViewBase from "../Base/FMViewBase";


/*
 * @Descripttion: layout
 * @version: 1.0.0
 * @Author: YeeChan
 * @Date: 2020-07-18 13:43:03
 */
const { ccclass, property, requireComponent, disallowMultiple, menu } = cc._decorator;



@ccclass
// @requireComponent(cc.ScrollView)
@disallowMultiple() //
//@menu('FM/FMItemLayout')
export default class FMItemLayout extends FMViewBase {


    @property({ tooltip: "", type: [cc.Node] })
    protected itemChilds: cc.Node[] = [];
    //
    protected ad_tag_csryw: any = null;

    protected itemIndex_csryw: number = 0;

    //item
    private _fmListenerData_csryw: FMListener;

    //
    public EventEnumView_csryw: {};


    /**
     * 
     * @param tag 
     */
    public setAdTag_csryw(tag: any) {
        this.ad_tag_csryw = tag;
    }

    protected addEvent_csryw(): void {

    }
    protected removeEvent_csryw(): void {

    }
    protected initView_csryw(): void {

    }

    /**
     * 
     * @param _listener 
     */
    public setFMListenerUpdate_csryw(_listener: FMListener) {
        this._fmListenerData_csryw = _listener;
    }


    /**
     * index
     * @param id 
     */
    public setItemIndex_csryw(id: number) {
        this.itemIndex_csryw = id;
        if (this._fmListenerData_csryw) {
            let childSum = this.itemChilds.length;
            for (let index = 0; index < childSum; index++) {
                let sum = this.itemIndex_csryw * childSum + index;
                let data = callFM_csryw(this._fmListenerData_csryw, sum);
                this.updateDataItemByIndex_csryw(data, index);
            }

        }
        // console.log("  " + id);
        // (this.itemChilds[0].getChildByName("label").getComponent(cc.Label)).string = id + "";
    }

    /**
     * 
     * @param data 
     * @param childIndex 
     */
    protected updateDataItemByIndex_csryw(data: any, childIndex: number) {
        LogUtils.log_csryw("FMItemLayout -> updateDataItemByIndex -> data", data);
    }

    /**
     *  
     */
    public getItemIndex_csryw(): number {
        return this.itemIndex_csryw;
    }

    public getPointX_csryw(): number {
        return this.node.x;
    }

    public getPointY_csryw(): number {
        return this.node.y;
    }

    public setPointX_csryw(x: number): void {
        this.node.x = x;
    }

    public setPointY_csryw(y: number): void {
        this.node.y = y;
    }

    /**
     * 
     */
    public getItemChildrenCount_csryw(): number {
        return this.itemChilds.length;
    }


}