    public static ttLogin_csryw(onSuccess: Function, onFail: Function) {
        if (AppPlatform.is_TT_GAME_csryw()) {
            window["tt"].login(
                {
                    force: false,
                    success: (res) => {
                        console.log(res)
                        console.log("1");
                        let code = res.code;
                        if (code) {
                            onSuccess(code);
                        } else {
                            console.log("code")
                            onFail();
                        }
                    },
                    fail: () => {
                        console.log("1");
                        onFail();
                    },
                })
            TTAPI.initRecord_csryw();
        }
    }

