    public static share_csryw(complate: Function = null) {

        if (!AppPlatform.is_TT_GAME_csryw()) {
            return;
        }
        window["tt"].shareAppMessage({
            templateId: AppConfig.tt_templateId_csryw,
            success() {
                if (complate != null) {
                    complate();
                }
            },
            fail() {
                console.log("");
            }
        });

    }

