    //-------------------------banner-------------------------------------------

    public static showBanner_csryw(): any {
        if (!AppPlatform.is_TT_GAME_csryw() || AppConfig.tt_bannerAdUnitId_csryw.length <= 0) {
            return;
        }
        if (!TTAPI._banner_csryw) {
            const { windowWidth, windowHeight } = window["tt"].getSystemInfoSync();
            var targetBannerAdWidth = 150;
            // 
            TTAPI._banner_csryw = window["tt"].createBannerAd({
                adUnitId: AppConfig.tt_bannerAdUnitId_csryw,
                adIntervals: 30,
                style: {
                    width: targetBannerAdWidth,
                    top: windowHeight - (targetBannerAdWidth / 16) * 9, // 
                }
            });
            // 
            TTAPI._banner_csryw.style.left = (windowWidth - targetBannerAdWidth) / 2;
            TTAPI._banner_csryw.onResize(size => {
                console.log(size.width, size.height);
                TTAPI._banner_csryw.style.top = windowHeight - size.height;
                TTAPI._banner_csryw.style.left = (windowWidth - size.width) / 2;
            });
        }
        TTAPI._banner_csryw.show();
    }

    public static hideBanner_csryw() {
        if (null != TTAPI._banner_csryw) {
            TTAPI._banner_csryw.hide();
        }
    }

    public static showMoreGamesModal_csryw(onSuccess: Function, onFail: Function) {
        const systemInfo = window["tt"].getSystemInfoSync();
        // iOS 
        if (systemInfo.platform !== "ios") {
            // 
            window["tt"].showMoreGamesModal({
                appLaunchOptions: [
                    {
                        appId: AppConfig.AppID_csryw,
                        query: "foo=bar&baz=qux",
                        extraData: {}
                    }
                    // {...}
                ],
                success(res) {
                    console.log("success", res.errMsg);
                    if (onSuccess) {
                        onSuccess();
                    }
                },
                fail(res) {
                    console.log("fail", res.errMsg);
                    if (onFail) {
                        onFail();
                    }
                }
            });
        }
        else {
            if (onFail) {
                onFail();
            }
        }
    }

