const { ccclass, property, menu, disallowMultiple } = cc._decorator;

/**
 * 
 */

@ccclass
@disallowMultiple() //
@menu('FrameWork/LoadingView')
export default class LoadingView extends cc.Component {
    //
    @property({ tooltip: "", type: cc.ProgressBar })
    private progressBar: cc.ProgressBar = null;

    onLoad() {
        this.setProcess_csryw(0);
    }

    /**
     * 
     * @param process  0-1
     */
    public setProcess_csryw(process: number) {
        if (process < 0) {
            process = 0;
        } else if (process > 1) {
            process = 1;
        }
        this.progressBar.progress = process;
    }


    /**
     *  
     */
    public getProcess_csryw(): number {
        return this.progressBar.progress
    }

}
