import AppPlatform from "../Util/AppPlatform";

/**
 * 
 */
export default class VibrateMgr {
    //
    public static isEnable_csryw = true;

    //
    public static vibrateShort_csryw(): void {
        if (!VibrateMgr.isEnable_csryw)
            return;
        let name = "";
        if (AppPlatform.is_TT_GAME_csryw()) {
            name = "tt";
        } else if (AppPlatform.is_WECHAT_GAME_csryw()) {
            name = "wx";
        } else if (AppPlatform.is_OPPO_GAME_csryw() || AppPlatform.is_VIVO_GAME_csryw()) {
            name = "qg";
        } else if (AppPlatform.is_QQ_PLAY_csryw()) { //qq
            name = "qq";
        }
        if (name != "") {
            window[name].vibrateShort();
        }
    }

    //
    public static vibrateLong_csryw(): void {
        if (!VibrateMgr.isEnable_csryw)
            return;

        let name = "";
        if (AppPlatform.is_TT_GAME_csryw()) {
            name = "tt";
        } else if (AppPlatform.is_WECHAT_GAME_csryw()) {
            name = "wx";
        } else if (AppPlatform.is_OPPO_GAME_csryw() || AppPlatform.is_VIVO_GAME_csryw()) {
            name = "qg";
        } else if (AppPlatform.is_QQ_PLAY_csryw()) { //qq
            name = "qq";
        }
        if (name != "") {
            window[name].vibrateLong();
        }
    }

    //,
    public static vibrate_csryw(time: number) {
        if (!VibrateMgr.isEnable_csryw) {
            return;
        }
        let num = 0;
        let count = time / 15;

        let funCall = null;
        funCall = function (cTime: number) {
            setTimeout(() => {
                VibrateMgr.vibrateShort_csryw();
                num++;
                if (num <= count) {
                    funCall(cTime);
                }
            }, cTime)
        }


        if (AppPlatform.is_TT_GAME_csryw()) {//
            count = time / 15;
            funCall(16)
        } else if (AppPlatform.is_WECHAT_GAME_csryw()) {
            count = time / 15; //15
            funCall(16)
        } else if (AppPlatform.is_OPPO_GAME_csryw()) {
            count = time / 20; //OPPO20
            funCall(21)
        } else if (AppPlatform.is_QQ_PLAY_csryw()) {//qq
            count = time / 20; //OPPO20
            funCall(21)
        }
    }
}
