import { ryw_Event } from "../Event/EventEnum";
import { LogUtils } from "../Util/LogUtils";

/**
 * 
 */
export default class EventMgr {
    private static eventTarget_csryw: cc.EventTarget = new cc.EventTarget();


    /**
     *  
     * @param name 
     * @param arg1 
     * @param arg2 
     * @param arg3 
     * @param arg4 
     * @param arg5 
     */
    public static emitEvent_csryw(name: ryw_Event, arg1?: any, arg2?: any, arg3?: any, arg4?: any, arg5?: any) {
        // LogUtils.info_csryw("" + name);
        this.eventTarget_csryw.emit(name + "", arg1, arg2, arg3, arg4, arg5);
    }
    /**
     *  
     *  `emit` 
     * @param name 
     * @param callback 
     * @param target 
     */
    public static onEvent_csryw(name: ryw_Event, callback: Function, target: any): void {
        this.eventTarget_csryw.on(name + "", callback, target);
    }

    /**
     *  
     * 
     * @param name 
     * @param callback 
     * @param target 
     */
    public static onceEvent_csryw(name: ryw_Event, callback: (arg1?: any, arg2?: any, arg3?: any, arg4?: any, arg5?: any) => void, target?: any): void {
        this.eventTarget_csryw.once(name + "", callback, target);
    }

    /**
     *  
     *  useCapture  name name 
     * @param name 
     * @param callback 
     * @param target 
     */
    public static offEvent_csryw(name: ryw_Event, callback?: Function, target?: any): void {
        this.eventTarget_csryw.off(name + "", callback, target);
    }

    /**
     * EventTargettarget 
         EventTarget  target 
         target  EventTarget 
     * @param target 
     */
    public static offTargetEvent_csryw(target: any): void {
        this.eventTarget_csryw.targetOff(target);
    }



}